* {
  box-sizing: border-box;
}

html {
  color: #060b35;
  font-family: Roboto, sans-serif;
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 17px 17px;
  transition: all .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card__img {
  width: 160px;
  height: 134px;
  display: block;
}

.card__product-name {
  margin-top: 40px;
  margin-bottom: 0;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__product-code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  line-height: 14px;
}

.card__product-evaluation {
  margin: 16px 0 24px 2px;
}

.card__product-evaluation, .card__product-price {
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.card__product-reviews {
  font-size: 10px;
}

.card__product-price-text, .card__product-price-value {
  line-height: 18px;
}

.card__product-price-text {
  color: #616070;
  font-size: 12px;
}

.card__product-price-value {
  font-size: 16px;
  font-weight: 700;
}

.card__product-button {
  color: #fff;
  text-align: center;
  cursor: pointer;
  background-color: #00acdc;
  border: 1px solid #0000;
  border-radius: 5px;
  width: 100%;
  margin-top: 16px;
  padding: 11px 0;
  font-family: Roboto, sans-serif;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  transition: all .3s;
}

.card__product-button:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

p {
  margin: 0;
}

.stars {
  justify-content: space-between;
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-child {
  margin-right: 0;
}

.stars--1 .stars__star:nth-child(-n+1), .stars--2 .stars__star:nth-child(-n+2), .stars--3 .stars__star:nth-child(-n+3), .stars--4 .stars__star:nth-child(-n+4), .stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
  position: relative;
  box-shadow: 0 2px 4px #0000000d;
}

.logo {
  margin-top: 4px;
  display: block;
}

.nav__list {
  align-items: center;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.nav__item:not(:last-child) {
  margin-right: 20px;
}

.nav__link {
  color: #060b35;
  text-align: center;
  background-color: #0000;
  padding: 23px 0;
  font-size: 12px;
  font-weight: 500;
  text-decoration: none;
  transition: all .3s;
  display: block;
  position: relative;
}

.nav__link.is-active, .nav__link:hover {
  color: #00acdc;
}

.nav__link.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  display: block;
  position: absolute;
  bottom: 0;
}

.catalog {
  --column-count: 1;
  grid-template-columns: repeat(var(--column-count), 200px);
  justify-content: center;
  gap: 48px 46px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .catalog {
    --column-count: 2;
  }
}

@media (width >= 768px) {
  .catalog {
    --column-count: 3;
  }
}

@media (width >= 1024px) {
  .catalog {
    --column-count: 4;
  }
}

* {
  font-family: Roboto, sans-serif;
}

body {
  margin: 0;
}

